import React from 'react'
import Link from 'gatsby-link'
import styles from './Nav.css'


const NavLink = ({
  className,
  ...props
}) => (
  <h3>
    <Link
      {...props}
      className={styles.nav}
    >
        {props.children}
    </Link>
  </h3>
)

const Nav = ({
  onNavigate,
  ...props
}) => (
  <nav {...props}>
      <NavLink activeStyle={{}}
        key='index'
        to={`/`}
        exact
        onClick={onNavigate}>Blog</NavLink>
      <NavLink activeStyle={{}}
        key='project'
        to={`/project`}
        onClick={onNavigate}>Project</NavLink>
      <NavLink activeStyle={{}}
        key='about'
        to={`/about`}
        onClick={onNavigate}>About</NavLink>
  </nav>
)

export default Nav



